#chat {
    width: 400px;
    overflow-y: auto;
    display: flex;
    flex-direction: column-reverse;
}

#chat::-webkit-scrollbar {
    display: none;
}

#chat ul {
    list-style-type: none;
    list-style-position: outside;
    padding: unset;
}

#chat blockquote {
    font-size: 1.2rem;
}


